.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.main-nav {
  font-family: "Oswald", "Helvetica", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #000000;
  position: relative;
  min-height: 66px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: flex-start;
}
@media (min-width: 767px) {
  .main-nav {
    flex-direction: row;
    justify-content: space-between;
    margin-left: 30px;
    margin-right: 30px;
  }
}
@media (min-width: 1220px) {
  .main-nav {
    margin-left: 110px;
    margin-right: 110px;
  }
}
.main-nav__toggle {
  position: absolute;
  width: 25px;
  height: 16px;
  top: 25px;
  right: 20px;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
@media (min-width: 767px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: -2px;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-nav--opened .main-nav__toggle {
  top: 25px;
  right: 33px;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  width: 25px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 768px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--closed .logo {
    display: block;
  }
  .main-nav--opened .main-nav__list {
    position: absolute;
    background-color: #ffffff;
    top: 0;
    right: 0;
    left: 0;
    z-index: 2;
  }
  .main-nav--opened .main-nav__link {
    text-align: center;
    width: 100vw;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__list {
    position: static;
    min-height: 0;
  }
  .main-nav--nojs .main-nav__link {
    display: block;
  }
}
.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  margin-top: 66px;
}
@media (min-width: 767px) {
  .main-nav__list {
    font-weight: 400;
    flex-direction: row;
    align-items: center;
    column-gap: 37px;
    margin-top: 15px;
  }
}
@media (min-width: 1220px) {
