  }
}
.main-nav__link {
  display: block;
  color: #000000;
  text-decoration: none;
  padding: 22px 0 24px 0;
  border-bottom: 1px solid #e6e6e6;
}
@media (min-width: 767px) {
  .main-nav__link {
    border-bottom: none;
    font-size: 18px;
    line-height: 24px;
    padding: 28px 0 18px;
    color: #000000;
  }
  .main-nav__link--active {
    position: relative;
    color: #000000;
    text-decoration: none;
  }
  .main-nav__link--active::after {
    position: absolute;
    content: "";
    right: 0;
    bottom: 10px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #68b738;
  }
}
@media (min-width: 1220px) {
  .main-nav__link {
    color: #000000;
  }
  .main-nav__link--active::after {
    background-color: #68b738;
  }
  .main-nav__link--index {
    color: #ffffff;
  }
  .main-nav__link--active-index {
    position: relative;
    color: #ffffff;
    text-decoration: none;
  }
  .main-nav__link--active-index::after {
    position: absolute;
    content: "";
    right: 0;
    bottom: 10px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #ffffff;
  }
}
.main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}
.main-nav__link--activ-index:hover {
  opacity: 0.6;
}
.main-nav__link--activ-index:active {
  opacity: 0.3;
}
